/* ProjectDetails.css */

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');

body {
  margin: 0;
  font-family: 'Inter', 'Segoe UI', sans-serif;
  background-color: #f8fafc;
  color: #1e293b;
}

.page-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  background-color: #f1f5f9;
  padding: 2rem 1rem;
  box-sizing: border-box;
}

.page-content.wide {
  width: 70vw;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  background-color: #f1f5f9; 
  border-radius: 16px;
  box-shadow: none;
  padding: 2rem;
}

.project-layout {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  width: 100%;
  align-items: flex-start;
  flex-wrap: wrap;
}

.project-left {
  flex: 1;
  min-width: 320px;
  background: white;
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.06); /* updated */
}


.project-middle {
  flex: 1;
  min-width: 320px;
  background: white;
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.06); /* updated */
}


.project-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 1.5rem;
}

.customer-name {
  font-size: 1.75rem;
  font-weight: 700;
  color: #0f172a;
  margin: 0;
}

.customer-name.highlight-name {
  font-size: 2rem;
  color: #1d4ed8;
}

.edit-link {
  font-size: 0.95rem;
  color: #2563eb;
  cursor: pointer;
  font-weight: 500;
  text-decoration: underline;
}

.edit-link:hover {
  color: #1d4ed8;
}

.edit-form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.25rem 2rem;
}

.edit-form label {
  display: flex;
  flex-direction: column;
  font-size: 0.9rem;
  font-weight: 500;
  color: #1e293b;
}

.edit-form input,
.edit-form select,
.edit-form textarea,
.task-form input,
.task-form select,
textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #cbd5e1;
  border-radius: 8px;
  font-size: 1rem;
  background-color: #fff;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
  font-family: 'Inter', sans-serif;
}

.edit-form input:focus,
.edit-form textarea:focus,
textarea:focus,
input:focus,
select:focus {
  outline: none;
  border-color: #2563eb;
  box-shadow: 0 0 0 2px rgba(37, 99, 235, 0.2);
}

.readonly {
  background-color: #f1f5f9;
  color: #64748b;
  cursor: not-allowed;
}

.task-form,
.form-actions {
  display: flex;
  gap: 0.75rem;
  margin-bottom: 1rem;
  flex-wrap: wrap;
}

button {
  background-color: #2563eb;
  color: white;
  padding: 0.6rem 1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.9rem;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  transition: background-color 0.2s ease, transform 0.2s ease;
}

button:hover {
  background-color: #1d4ed8;
  transform: scale(1.02);
}

.task-group {
  margin-top: 1.5rem;
}

.task-headers {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 2fr auto;
  font-size: 0.9rem;
  color: #475569;
  padding: 0.5rem 0;
  border-bottom: 2px solid #cbd5e1;
  font-weight: 600;
}

.task-row {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 2fr auto;
  align-items: center;
  gap: 1rem;
  padding: 0.75rem 0;
  border-bottom: 1px solid #e2e8f0;
  font-size: 0.95rem;
}

/* ✅ Patch: Hover effect for task-row */
.task-row:hover {
  background-color: #f1f5f9;
  transition: background-color 0.2s ease;
}

.task-status .status-badge {
  padding: 0.3rem 0.75rem;
  border-radius: 9999px;
  font-size: 0.8rem;
  font-weight: 600;
  display: inline-block;
  text-transform: capitalize;
}

.status-badge.not-started {
  background-color: #e0f2fe;
  color: #0369a1;
}

.status-badge.in-progress {
  background-color: #fef9c3;
  color: #b45309;
}

.status-badge.completed {
  background-color: #dcfce7;
  color: #15803d;
}

.status-badge.cancelled\/on-hold {
  background-color: #fef2f2;
  color: #991b1b;
}

.task-actions button {
  background: none;
  color: #2563eb;
  padding: 0.25rem;
  font-size: 0.9rem;
  border: none;
  cursor: pointer;
}

.task-actions button:hover {
  color: #1d4ed8;
  text-decoration: underline;
}

.section-card h3 {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1e293b;
  margin-bottom: 1rem;
}

.loader,
.not-found {
  font-size: 1.2rem;
  text-align: center;
  margin-top: 2rem;
}

.back-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background-color: #e2e8f0;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  text-decoration: none;
  color: #1e293b;
  font-weight: 500;
  margin-bottom: 1.5rem;
  transition: background-color 0.2s ease;
}

.back-btn:hover {
  background-color: #cbd5e1;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(15, 23, 42, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.modal {
  background: #fff;
  padding: 2rem;
  border-radius: 16px;
  max-width: 480px;
  width: 100%;
  max-height: 90vh; /* 👈 limits height */
  overflow: hidden;  /* 👈 prevents second scrollbar */
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.15);
  animation: slideFadeIn 0.3s ease-out;
  display: flex;
  flex-direction: column;
}

@keyframes slideFadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.toggle-completed-btn {
  background-color: #e2e8f0;
  color: #1e293b;
  padding: 0.4rem 0.75rem;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 500;
  border: none;
  cursor: pointer;
  margin-left: 1rem;
  transition: background-color 0.2s ease;
}
.dropdown {
  position: relative;
  z-index: 10; /* allow dropdown to appear over other elements */
  background-color: #fff;
}

.edit-form {
  overflow: visible; /* ensure no cutoff */
}

.section-card,
.project-left {
  overflow: visible; /* fix for dropdown cutoff in small containers */
}

html, body {
  height: auto;
  overflow-y: auto;
}


.toggle-completed-btn:hover {
  background-color: #cbd5e1;
}

.log-entry {
  background-color: #f8fafc;
  border: 1px solid #e2e8f0;
  padding: 0.75rem 1rem;
  border-radius: 10px;
  margin-bottom: 0.75rem;
  font-size: 0.95rem;
  line-height: 1.4;
}

.log-empty {
  font-style: italic;
  color: #94a3b8;
  padding: 1rem;
}

.logs-list {
  list-style-type: none;
  padding-left: 0;
}

.log-entry {
  background-color: #f8fafc;
  border: 1px solid #e2e8f0;
  padding: 0.75rem 1rem;
  border-radius: 10px;
  margin-bottom: 0.75rem;
  font-size: 0.95rem;
  line-height: 1.4;
  position: relative;
    word-break: break-word;
  white-space: pre-wrap;
}

.log-icons {
  position: absolute;
  top: 0.75rem;
  right: 1rem;
  display: flex;
  gap: 0.5rem;
}

.log-icons button {
  background: none;
  border: none;
  color: #2563eb;
  cursor: pointer;
  font-size: 0.9rem;
  padding: 0;
}

.log-icons button:hover {
  color: #1d4ed8;
}

.log-textarea {
  width: 100%;
  padding: 0.6rem;
  font-family: inherit;
  border-radius: 8px;
  border: 1px solid #cbd5e1;
  resize: vertical;
}

.log-actions {
  display: flex;
  gap: 0.5rem;
  margin-top: 0.5rem;
}

.section-divider {
  border-top: 1px solid #e2e8f0;
  margin: 2rem 0 1rem;
}

.section-heading {
  font-size: 1.35rem;
  font-weight: 600;
  color: #0f172a;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.project-tasks,
.meeting-minutes-section {
  background: white;
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.06); /* new if not yet present */
}

.project-left:hover,
.project-middle:hover,
.project-tasks:hover,
.meeting-minutes-section:hover {
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
  transition: box-shadow 0.2s ease;
}
input:hover,
select:hover,
textarea:hover {
  border-color: #93c5fd; /* light blue border */
  background-color: #f0f9ff; /* subtle blue background */
  transition: all 0.2s ease-in-out;
}

.section-heading {
  font-size: 1.35rem;
  font-weight: 600;
  color: #0f172a;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1.25rem;
}

/* ✅ Patch 6: Section Fade + Slide Animation */
.fade-in-section {
  animation: fadeSlideIn 0.4s ease-out;
  will-change: opacity, transform;
}

@keyframes fadeSlideIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ✅ Patch 7: Button Variants */
button.primary {
  background-color: #2563eb;
  color: white;
}

button.primary:hover {
  background-color: #1d4ed8;
}

button.secondary {
  background-color: #f1f5f9;
  color: #1e293b;
  border: 1px solid #cbd5e1;
}

button.secondary:hover {
  background-color: #e2e8f0;
  color: #0f172a;
}

/* ✅ Patch 8: Card Hover Elevation */
.project-left,
.project-middle,
.project-tasks,
.meeting-minutes-section {
  transition: box-shadow 0.3s ease, transform 0.3s ease;
}

.project-left:hover,
.project-middle:hover,
.project-tasks:hover,
.meeting-minutes-section:hover {
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transform: translateY(-4px);
}
